
  import Vue from "vue";
  import {Component} from "vue-property-decorator";
  import store from "@/store/index";

  @Component
  export default class Label extends Vue {
    get tags() {
      return store.state.tagList;
    }

    created() {
      store.commit('fetchTags')
    }

    createTag() {
      store.commit('createTag');
    }
  }
